import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { ethers } from "ethers";
import {
  Box,
  Button,
  Typography,
  Grid,
  TextField,
  FormControl,
  Select,
  TableCell,
  TableRow,
  CircularProgress,
  Link,
  LinearProgress,
} from "@material-ui/core";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import { useAddress, useWeb3Context } from "src/hooks/web3Context";
import { abi as nftABI } from "../../abi/nft.json";
import { abi as minter0ABI } from "../../abi/minter.json";
import { error, success } from "src/slices/MessagesSlice";
import store from "src/store";
import "./style.css";
import La1 from "./la1.png";

const launchTime = Date.parse("16 Apr 2022 13:00:00 UTC");

const nftAddr = "0x6132E9596ec9A4eDD43f337FA6979EBfc13Ff47d";
const minter0Addr = "0x0BAeca1f4dEcBaDF5e6B9973DD7f881BFa98C856";

const mintFee = 0.002;

function ConnectMenu() {
  const { search } = useLocation();

  const { connect, disconnect, hasCachedProvider, provider, chainID, connected, uri, web3 } = useWeb3Context();
  const address = useAddress();
  const [isConnected, setConnected] = useState(connected);
  const [freeCount, setFreeCount] = useState(-1);
  const [payCount, setPayCount] = useState(-1);
  const [isMinted, setIsMinted] = useState(false);
  const [nftBal, setNftBal] = useState(0);
  const [inputAmount, setInputAmount] = useState(0);
  const [loading, setLoading] = useState(false);
  const [remainTime, setRemainTime] = useState(0);

  let buttonText = "Connect Wallet";
  let clickFunc = connect;

  if (isConnected) {
    buttonText = "Disconnect";
    clickFunc = disconnect;
  }

  const getTimeElement = (rTime, id) => {
    switch (id) {
      case 0:
        return Math.floor(rTime / 86400);
      case 1:
        return Math.floor((rTime % 86400) / 3600);
      case 2:
        return Math.floor((rTime % 3600) / 60);
      case 3:
        return Math.floor(rTime % 60);
      default:
        return "";
    }
  };

  const isPay = () => {
    return freeCount >= 1000 || isMinted;
  };

  const handleMint = async () => {
    if (!address) {
      store.dispatch(error("Please connect to Ethereum!"));
      return;
    } else if (!(inputAmount == 1 || inputAmount == 2 || inputAmount == 3 || inputAmount == 4 || inputAmount == 5)) {
      store.dispatch(error("Invalid nft amount! It should be 1, 2, 3, 4 or 5."));
      return;
    } else {
      const minter0Cont = new ethers.Contract(minter0Addr, minter0ABI, provider.getSigner());
      try {
        setLoading(true);
        if (isPay()) {
          let val = (mintFee * inputAmount).toString();
          let buffer = await minter0Cont.mint(inputAmount, { value: ethers.utils.parseUnits(val) });
          await buffer.wait();
        } else {
          let buffer = await minter0Cont.mint(2, { value: ethers.utils.parseUnits("0.0") });
          await buffer.wait();
        }
        setLoading(false);
        store.dispatch(success(`Mint Success!`));
      } catch (e) {
        console.error(e);
        setLoading(false);
      }
    }
    setLoading(false);
  };

  const fetchData = async () => {
    try {
      if (address) {
        const nftCont = new ethers.Contract(nftAddr, nftABI, provider.getSigner());
        let data0, data1;
        data0 = await nftCont.freeCount();
        setFreeCount(Number(data0));
        data1 = await nftCont.totalSupply();
        setPayCount(Number(data1) - Number(data0));
        data0 = await nftCont.mintedFreeAmount(address);
        setIsMinted(Number(data0) == 2);
        data0 = await nftCont.balanceOf(address);
        setNftBal(Number(data0));
      }
    } catch (e) {
      console.error(e);
    }
  };

  useEffect(() => {
    setConnected(connected);
  }, [web3, connected]);

  useEffect(() => {
    fetchData();
  }, [connected, address]);

  return (
    <Box className="background-image" style={{ padding: "20px 30px 50px", position: "relative" }}>
      <Grid container justifyContent="center" alignItems="center">
        <Grid item xs={12} sm={11} style={{ padding: "10px" }}>
          <Box display="flex" alignItems="center" justifyContent="flex-end" className="header-bar">
            {/* <img src={Logo} alt="Logo" style={{ position: "absolute", left: "40px", height: "70px" }} /> */}
            {address && (
              <Box
                sx={{
                  margin: "0px 20px",
                  color: "white",
                  backgroundColor: "black",
                  padding: "0px 5px",
                  borderRadius: "3px",
                }}
              >
                <Typography variant="h6">Your NFT Balance: {nftBal}</Typography>
              </Box>
            )}

            <Box
              sx={{
                margin: "0px 20px",
                color: "white",
                backgroundColor: "black",
                padding: "0px 5px",
                borderRadius: "3px",
              }}
            >
              <Typography variant="h6">
                {address
                  ? `${address.slice(0, 6)}...${address.slice(address.length - 4, address.length)}`
                  : "No Wallet"}
              </Typography>
            </Box>
            <Button
              variant="contained"
              color="success"
              size="small"
              onClick={clickFunc}
              style={{
                backgroundColor: "#e71212",
                borderRadius: "10px",
                padding: "20px",
                marginRight: "50px",
                color: "white",
                borderTop: "2px solid #eddd06",
              }}
            >
              {buttonText}
            </Button>
          </Box>
        </Grid>
      </Grid>

      <Box display="flex" justifyContent="space-around" alignItems="center" mt={2}>
        <Box>
          <Box textAlign="center">
            <Box display="flex" justifyContent="center" alignItems="center" mb={3} style={{ color: "red" }}>
              <Typography variant="h3" style={{ margin: "0px 10px" }}>
                Free: {freeCount == -1 ? "___" : freeCount}/1000
              </Typography>
              <Typography variant="h3" style={{ margin: "0px 10px" }}>
                Payable: {payCount == -1 ? "___" : payCount}/3000
              </Typography>
            </Box>
            <Typography variant="h4" style={{ color: "#fcff1b" }}>
              {isPay() ? "You can mint up to 5 nfts in one transaction" : "You can mint 2 nfts free per wallet"}
            </Typography>
            <Box display="flex" justifyContent="space-around" alignItems="center" mt={3}>
              {isPay() && (
                <TextField
                  type="number"
                  style={{ fontSize: "20px", marginRight: "10px", borderRadius: "5px", backgroundColor: "#dddddd" }}
                  placeholder="nft amount(1-5)"
                  onChange={ev => setInputAmount(Number(ev.target.value))}
                />
              )}
              <Button className="pool-button" onClick={handleMint}>
                Mint
              </Button>
            </Box>
          </Box>
        </Box>
        <Box>
          <img src={La1} alt="picture" style={{ width: "100%", maxHeight: "70vh" }} />
        </Box>
      </Box>

      {loading && (
        <Box className="loading">
          <CircularProgress style={{ color: "aqua" }} />
        </Box>
      )}

      {/* {remainTime > 0 && (
        <Box className="loading">
          <Box className="timer" display="flex" justifyContent="space-around" alignItems="center">
            <Typography variant="h2">{getTimeElement(remainTime, 0)} D</Typography>
            <Typography variant="h2">:</Typography>
            <Typography variant="h2">{getTimeElement(remainTime, 1)} H</Typography>
            <Typography variant="h2">:</Typography>
            <Typography variant="h2">{getTimeElement(remainTime, 2)} M</Typography>
            <Typography variant="h2">:</Typography>
            <Typography variant="h2">{getTimeElement(remainTime, 3)} S</Typography>
          </Box>
        </Box>
      )} */}
    </Box>
  );
}

export default ConnectMenu;
